import React, { useEffect } from "react"
import PropTypes from "prop-types"
import "../assets/scss/app.scss"
import Header from "./header"
import Loader from "./loader"
import Helmet from "react-helmet"

const Layout = ({ children }) => {
  useEffect(() => {
    if (typeof window === "undefined") {
      window.$ = require("jquery")
      window.jQuery = $
      require("pagepiling.js")
      require("bootstrap")
      require("typed.js")
      require("jquery-countto")
      require("../assets/js/isotope.pkgd")
      require("magnific-popup")
      require("owl.carousel")
      require("../assets/js/nill")
    }
  })

  return (
    <>
      <Helmet>
        <body
          className="pilling-page"
          data-spy="scroll"
          data-target="#scrollspy"
          data-offset="1"
        />
        {/* <script
          src="https://cdnjs.cloudflare.com/ajax/libs/jquery/3.4.1/jquery.min.js"
          type="text/javascript"
        />
        <script
          src="https://cdnjs.cloudflare.com/ajax/libs/pagePiling.js/1.5.6/jquery.pagepiling.min.js"
          type="text/javascript"
        />
        <script
          src="https://cdnjs.cloudflare.com/ajax/libs/twitter-bootstrap/4.4.1/js/bootstrap.min.js"
          type="text/javascript"
        />
        <script
          src="https://cdnjs.cloudflare.com/ajax/libs/typed.js/2.0.11/typed.min.js"
          type="text/javascript"
        />
        <script
          src="https://cdnjs.cloudflare.com/ajax/libs/jquery-countto/1.2.0/jquery.countTo.min.js"
          type="text/javascript"
        />
        <script
          src="https://cdnjs.cloudflare.com/ajax/libs/jquery.isotope/3.0.6/isotope.pkgd.min.js"
          type="text/javascript"
        />
        <script
          src="https://cdnjs.cloudflare.com/ajax/libs/magnific-popup.js/1.1.0/jquery.magnific-popup.min.js"
          type="text/javascript"
        />
        <script
          src="https://cdnjs.cloudflare.com/ajax/libs/OwlCarousel2/2.3.4/owl.carousel.min.js"
          type="text/javascript"
        /> */}
      </Helmet>
      <Loader></Loader>
      <div id="pagepiling" className="pagepiling">
        <Header></Header>
        {children}
      </div>
    </>
  )
}

Layout.propTypes = {
  children: PropTypes.node.isRequired,
}

export default Layout
